<template>
    <div class="personal-content-body">
        <div class="content">
            <div class="content-title clear">
                <div class="left">政策速递</div>
                <el-input placeholder="搜索" v-model="keyWord" class="header-right-time right" :clearable='true' @change="getListData(1)">
                    <i slot="prefix" class="el-input__icon el-icon-search "></i>
                </el-input>
                <!-- <el-date-picker v-model="selectTime" type="date" placeholder="选择日期" :clearable='true' value-format="yyyy-MM-dd" @change="toselectTime" class="header-right-time right">
                </el-date-picker> -->
            </div>
            <div class="historyapply-group">
                <div class="clear table-group-title">
                    <!-- <div class="breadcrumb">
                        <el-breadcrumb separator-class="el-icon-arrow-right">
                            <el-breadcrumb-item :to="{name:'tuneUpAssistantList'}">尽调助手</el-breadcrumb-item>
                            <el-breadcrumb-item>政策速递</el-breadcrumb-item>
                        </el-breadcrumb>
                    </div> -->
                    <!-- <el-input placeholder="搜索" v-model="keyWord" class="header-right-time left" :clearable='true' @change="getListData(1)">
                        <i slot="prefix" class="el-input__icon el-icon-search "></i>
                    </el-input> -->
                    <div class="line"></div>
                </div>
                <div class="historyapply-group-tablebox">
                    <div class="table-normal-box">
                        <div class="qyyq-item-box" v-for="(item,index) of list " :key="index" >
                            <h1 @click="toDeatails(item.id)">{{item.title}}</h1>
                            <div class="btn">
                                <span class="card card_6">{{item.source}}</span>
                                <span>{{item.time}}</span>
                            </div>
                        </div>
                        <div class="table-nodata flex_center" v-if="total==0">
                            <img src="../../images/table_nodata.png"> 
                            <span>没有对应数据</span>
                        </div>
                    </div>
                    <div class="pagination-normal" v-if="total!=0">
                        <el-pagination   background layout="prev, pager, next" :total="total" :page-size="pageSize" prev-text="上一页" next-text="下一页" @current-change="currentChange" :current-page="currentPage">
                        </el-pagination>
                        <!-- <el-pagination  background layout="prev, pager, next" :total="total" :page-size="pageSize" prev-text="上一页" next-text="下一页" @current-change="currentChange" :current-page="currentPage" >
                        </el-pagination> -->
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {
        policyNews,
    } from "../../config/api";
    import {
        dialogConfirmHasbtn,
        dialogConfirm
    } from "../../config/mUtils";
    export default {
        data() {
            return {
                currentPage: 1,
                keyWord: "",
                pageSize: 10,
                total: 0,
                selectTime: "",
                list:[],
            };
        },
        created() {
            var page = 1;
            if (this.$route.query.page) {
                page = parseInt(this.$route.query.page);
            }
            this.getListData(page);
        },
        mounted() {},
        methods: {
            currentChange(val) {
                if (this.total > this.pageSize) {
                    this.getListData(val);
                }
            },
            toDeatails(id) {
                this.$router.push({
                    name: 'jdzsHomepageZcsdDetails',
                    query: {
                        id:id,
                        total:this.total,
                        keywords:this.keyWord,
                        page:this.currentPage
                    }
                })
            },
            // toselectTime() {},
            getListData(val) {
                this.currentPage = val;
                let params = {
                    page: val,
                    pageSize: this.pageSize
                };
                if (this.keyWord != null && this.keyWord != "") {
                    params.keywords = this.keyWord;
                }
                policyNews(params).then(res => {
                    // console.log(res);
                    this.list = res.data.payload.data;
                    this.total = res.data.payload.total;
                });
            }
        }
    };
</script>

<style lang="scss" scoped>
    @import "../../style/modal.scss";
    .content {
        width: 100%;
        min-height: 100%;
        min-width: 1000px;
        background-color: white;
        padding: 20px 30px;
    }
    
    .content-title {
        width: 100%;
    }
    
    .histroyapply-icon {
        width: 15px;
        height: 15px;
        background: url("../../images/histroyapply_icon.png") no-repeat;
        display: inline-block;
        float: left;
        margin: 2px 6px 0px 0px;
    }
    
    .historyapply-group {
        width: 100%;
        // border: 1px solid #e4e4e4;
        // padding: 18px 0 16px;
        .table-group-title{
            padding: 0;
        }
        .historyapply-group-tablebox{
            padding: 0;
        }
    }
    
    .historyapply-group-total {
        font-size: 18px;
        color: #3c98e8;
        font-weight: bold;
        line-height: 1em;
        margin-bottom: 35px;
    }
    
    .historyapply-group-tablebox {
        // padding: 0px 18px 0px 14px;
        padding: 0px 18px;
        min-height: 440px;
    }
    
    .table-normal-box {
        min-height: 440px;
    }
    
    .pagination-normal {
        text-align: center;
        margin-top: 20px;
    }
    
    .header-right-time {
        margin-right: 10px;
        position: relative;
    }
    
    .header-right-time:nth-child(2) {
        margin-right: 0px;
    }
    
    .creditCz-box span:hover {
        cursor: pointer;
        color: #3c98e8;
    }
    
    .creditCz-box span:nth-child(2) {
        //padding: 0 10px;
    }
    
    .btn-cancle {
        border: 1px solid #3c98e8;
        color: #3c98e8;
        margin-right: 20px;
    }
    
    .form-group-inputbox {
        width: 470px;
        margin-right: 0px;
    }
    
    .breadcrumb {
        margin-top: 19px;
        //   padding: 0px 14px;
    }
    
    .table-group-title-btn {
        display: flex;
        align-items: center;
        img {
            margin-right: 8px;
        }
    }
    
    .historyapply-group-top-dropdown {
        margin-right: 10px;
    }
    
    .qyyq-item-box {
        margin-top: 24px;
        border-bottom: 1px solid #f1f1f1;
        padding-bottom: 20px;
        h1 {
            font-size: 18px;
            // font-weight: bold;
            color: #35383a;
            margin-bottom: 14px;
            cursor: pointer;
        }
        h1:hover {
            color: #3c98e8;
        }
        span {
            font-size: 12px;
            color: #a0a4aa;
            margin-right: 20px;
        } // span:first-child{
        //   margin-right: 3px;
        // }
        img {
            width: 12px;
            height: 12px;
            margin-right: 10px;
        }
        .qyyq-item-content {
            font-size: 14px;
            color: #a0a4aa;
            margin-bottom: 14px;
        }
        .btn {
            display: flex;
            align-items: center;
        }
        .dian {
            width: 4px;
            height: 4px;
            background-color: #3c98e8;
            border-radius: 50%;
            margin-right: 3px;
        }
        .card {
            padding: 4px 11px;
            margin-right: 16px;
        }
        .card_1 {
            color: #d32f2e;
            background-color: #fbeaea;
        }
        .card_2 {
            color: #c67114;
            background-color: #f9f0e7;
        }
        .card_3 {
            color: #f7b62b;
            background-color: #fef7e9;
        }
        .card_4 {
            color: #9d9d9d;
            background-color: #f5f5f5;
        }
        .card_6 {
            color: #1787fb;
            background-color: rgba(23, 135, 251, 0.1);
        }
    }
    .table-nodata{
    		height: 300px;
    }
</style>



